@import url(https://fonts.googleapis.com/css2?family=Fira+Sans:wght@100;200;300;400;500;600;700&display=swap);
.navbar{display:flex;align-items:center;height:auto;padding:1em 1.5em;background-color:#e1e4e4}.navbar .menu{display:none}.navbar h2{margin:0 1.5rem 0 1rem}.navbar .navItems{min-width:320px;display:flex;justify-content:space-between}.navbar .navItems a{text-decoration:none;color:#2a2c30;transition:0.5s;line-height:30px}.navbar .navItems a:hover{color:#ccc012}.navbar .navItems .search{height:30px;display:flex}.navbar .navItems .search .searchInput{width:100px;padding:0 0.6rem;height:100%;outline:none;border-radius:4px;border:1px solid #e1e4e4}.navbar .navItems .search .btn{height:100%;border-radius:4px;border:1px solid #e1e4e4}.sideDrawer{display:none}.pagination{max-width:300px;margin:0 auto 1.5em}.pagination ul{margin:0 auto;padding:0;display:flex;justify-content:space-around;list-style:none}.pagination ul li{cursor:pointer;padding:0.5rem;-webkit-clip-path:circle();clip-path:circle();transition:0.3s}.pagination ul li:hover{background-color:#ccc012}@media screen and (min-width: 481px) and (max-width: 768px){h2{font-size:0.9em;margin:0}}@media screen and (max-width: 480px){.navbar .menu{display:block}.navbar h2{display:none}.navbar .navItems{display:none}.sideDrawer{display:block;height:100%;min-width:80%;position:fixed;left:0;top:0;transform:translateX(-100%);background-color:#fff;padding:32px 16px;box-sizing:border-box;z-index:100;transition:0.4s 0.1s}.sideDrawer .menuClose{position:absolute;right:20px;top:20px;font-size:2em}.sideDrawer .navItems{margin-top:2em;display:flex;flex-direction:column;height:130px;justify-content:space-between}.sideDrawer .navItems a{text-decoration:none;color:#2a2c30;transition:0.5s;line-height:30px}.sideDrawer .navItems a:hover{color:#ccc012}.sideDrawer.active{transform:translateX(0)}.backdrop{position:fixed;top:0;width:100%;height:100%;z-index:50;transition:0.4s;background-color:rgba(42,44,48,0.364706)}}

.wrap{max-width:80%;margin:2em auto;display:grid;grid-template-columns:repeat(4, 25%);grid-template-rows:repeat(3, 1fr)}.wrap .videoContainer{position:relative;max-width:100%;padding:0.6rem;max-height:300px}.wrap .videoContainer::before{position:absolute;top:0;left:0;content:'';background-color:rgba(220,225,233,0.356863);width:100%;height:100%;visibility:hidden;opacity:0;transition:0.5s;pointer-events:none}.wrap .videoContainer:hover::before{visibility:visible;opacity:1}.wrap .videoContainer a{text-decoration:none;color:#2a2c30}.wrap .videoContainer figure{max-width:100%;margin:0}.wrap .videoContainer figure img{object-fit:contain;max-width:100%}.wrap .videoContainer .timeAndLike{display:flex;justify-content:space-between}.wrap .videoContainer .timeAndLike span:nth-of-type(2){cursor:pointer}.wrap .videoContainer .videoDetail{width:100%}.wrap .videoContainer .videoDetail h4{margin:0 0 1rem;display:block;overflow:hidden;text-overflow:ellipsis;display:-webkit-box;-webkit-line-clamp:1;-webkit-box-orient:vertical}.wrap .videoContainer .videoDetail span{color:#6e6e6e;display:block;overflow:hidden;height:40%;text-overflow:ellipsis;display:-webkit-box;-webkit-line-clamp:3;-webkit-box-orient:vertical}@media screen and (min-width: 769px) and (max-width: 1025px){.wrap{grid-template-columns:repeat(3, 33.3%);grid-template-rows:repeat(4, 1fr)}}@media screen and (min-width: 481px) and (max-width: 768px){.wrap{grid-template-columns:repeat(2, 50%);grid-template-rows:repeat(6, 1fr)}}@media screen and (max-width: 480px){.wrap{max-width:70%;grid-template-columns:repeat(1, 100%);grid-template-rows:repeat(12, 1fr)}}

.videoPlayWrap{margin:0 auto;position:relative}.videoPlayWrap .goBack{position:absolute;left:2em;top:250px;cursor:pointer;transition:0.3s}.videoPlayWrap .goBack:hover{color:#ccc012}.videoPlayWrap .goBack svg{font-size:2.5em}.videoPlayWrap .videoContainer{display:flex;flex-direction:column;margin:2em 5em}.videoPlayWrap .videoContainer .videoDetail{padding:0.6rem}.videoPlayWrap .videoContainer .videoDetail .duration{font-size:1.2rem}.videoPlayWrap .videoContainer .videoDetail h4{font-size:1.6rem}.videoPlayWrap .videoContainer .videoDetail .description{word-break:normal;white-space:pre-line;line-height:2rem}@media screen and (max-width: 480px){.videoPlayWrap .goBack{display:none}.videoPlayWrap .videoContainer{margin:0}}

body{margin:0;font-family:'Fira Sans', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}

